const Article = (props) => {
    const { urlToImage, url, description, source, title } = props.article;
    let img;

    if(urlToImage !== ''){
        img = <div className="card-image">
                    <img src={urlToImage} alt={title} className="card-img-top"/>
                </div> ;
    } else {
        img = null;
    }

    
    return (
        <div className="col-md-6 col-12 mb-4">
            <div className="card">
                { img }
                
            </div>
        </div>
    );
};

export default Article;